/*Estilo para tablets*/
@media only screen and (min-width: 768px) and (max-width: 959px) {
  /*Qualidade Home*/
  .qualidade:after {
    right: 66px;
  }

  .qualidade_lista li {
    padding: 0 10px;
  }

  /*Footer*/

  .footer_redes ul li a {
    border: 3px solid $cor-primaria;
    display: block;
    padding: 6px;
  }

  .footer_redes ul li a img {
    width: 26px;
    height: 26px;
  }

  /*Produtos*/

  .produto_item h2 {
    top: -150px;
    margin-bottom: -56px;
  }

  .produto_icone {
    padding: 41px 0;
  }

  .produto_info p {
    @include type-1(14);
    height: 122px;
    padding: 20px 30px;
  }

  .produto_info ul li {
    width: 181px;
    height: 49px;
  }
} /* Final do estilo para tablets */

/*Estilo para smartphone*/
@media only screen and (max-width: 767px) {
  /*Header*/
  .header {
    position: relative;
    padding-bottom: 0px;
  }

  .header img {
    margin: 0 auto 10px auto;
  }

  .header_menu {
    text-align: center;
  }

  .header_menu ul li {
    margin: 5px;
  }

  .header_menu ul li a {
    border: 4px solid $cor-preto-puro;
    width: 136px;
    display: block;
    float: left;
  }

  .header_menu ul li a:hover {
    border-color: $cor-branco-puro;
  }

  .header_menu ul li a.menu_ativo {
    border-color: $cor-branco-puro;
  }

  /*Introducao*/

  .introducao h1 {
    @include type-1(36);
  }

  .introducao {
    margin-top: 0px;
    padding-top: 40px;
    background: url('../img/bg-mobile.jpg') no-repeat center;
    background-size: cover;
  }

  /*Introdução Interna*/

  .introducao-interna {
    margin-top: 0px;
  }

  /*Call*/
  .call {
    padding-top: 10px;
  }

  /*Qualidade Home*/
  .qualidade:after {
    display: none;
  }

  /*Sobre*/

  .missao_sobre p {
    @include type-1(14);
    padding-right: 0px;
  }

  .missao_sobre ul li {
    @include type-1(14);
    line-height: 25px;
  }

  /*Produtos*/

  .produto_item h2 {
    top: -100px;
    margin-bottom: -68px;
  }

  .produto_icone {
    padding: 41px 0;
  }

  .produto_info p {
    @include type-1(14);
    height: auto;
    padding: 20px 30px;
  }

  .produto_info ul li {
    width: 149px;
    height: 49px;
    @include type-1(14);
    padding-top: 14px;
  }

  .form {
    padding-right: 0px;
  }

  .orcamento_dados ul {
    padding-right: 0px;
  }

  /*Contato*/

  .contato_form {
    padding-right: 0px;
    margin-bottom: 40px;
  }
} /* Final do estilo para smartphone */
